
#title {
    font-family: Stencil;
}

.text-style {
    font-family: Stencil;
}
.game-options {
    padding-left: 5%;
    padding-top: 2%;
}
.start-area {
    padding-top: 2%;
}
.input-text {
    width: 100px;
}
@media only screen and (max-width: 500px) {
.game-options {
        padding-left: 0;
}
}
.game-info {
    font-family: Arial;
    display:inline;
}
.custom-error {
    color: crimson;
}
.errors-div {
    padding-top: 2%;
}

.board-column {
    float: left;
    border: 1px solid;
    margin: 2px;
}
.board {
    border: 1px solid;
    background-color: darkcyan;
}
.full-board {
    padding-top: 3%;
    padding-bottom: 3%;
}
.board-row {
    page-break-inside: avoid;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.game-cell {
    width: 20px;
    height: 20px;
    background-color: dimgray;
}
.game-cell-interface {
    width: 20px;
    height: 20px;
    background-color: bisque;
}
.game-bomb {
    height: auto;
    width: auto;
}
.game-cell-flag {
    width: 20px;
    height: 20px;
}
img {
    max-width: 100%;
    max-height: 100%;
}
.text-style {
    font-family: Stencil;
}
